<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title" v-if="this.$store.state.orderInfo.id">
          <span> {{ this.$store.state.orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;
             . &nbsp;    &nbsp; .
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary" v-if="this.$store.state.orderInfo">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ cartOrderInfoForm.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ cartOrderInfoForm.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ cartOrderInfoForm.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ cartOrderInfoForm.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                  
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block" v-if="this.$store.state.orderInfo">
          <ul class="cart__orders">
            <CartOrder v-for="item in cartOrderInfoForm.basket.items" :key="item.id" :item="item"/>
          </ul>
          <CartTotal
            :amount="totalAmount"
            :price="cartOrderInfoForm.totalPrice | numberFormat"/>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters } from 'vuex';
import CartOrder from '@/components/CartOrder.vue';
import CartTotal from '@/components/CartTotal.vue';
import numberFormat from '@/helpers/numberFormat';

export default {
  components: {
    CartOrder,
    CartTotal,
  },
  computed: {
    ...mapGetters(
      { cartOrderInfoForm: 'cartOrderInfo', totalAmount: 'cartOrderInfoTotalAmount' },
    ),
  },
  filters: {
    numberFormat,
  },
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      return;
    }

    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
};
</script>
